@import "mixin";

@media all and (max-width: 720px) {

	body {
		min-width: $w375;
		margin: auto; }
	.bunner {
		background: $white;
		max-width: $w375;
		margin: auto;
		position: relative;
		.ninja {
			background: url("../image/Menu-1.png") center center no-repeat; }
		nav {
			height: 2140px;
			a {
				color: $white; }
			.logo {
				div {
					color: $white; } } }
		.st-effect-1.st-menu-open .st-effect-1.st-menu {
			visibility: visible;
			transform: translate3d(340%, 0, 0);
			z-index: 300; } }
	// .site-h
	// 	margin: 40px 0 20px

	.what-we-do {
		width: $w375;
		margin-bottom: 0;
		.site-h {
			margin: 40px 0 20px;
			font-size: 22px;
			text-align: center;
			.block {
				font-size: 14px; } }
		.artcl:nth-child(2),
		.artcl:nth-child(3),
		.artcl:nth-child(4) {
			float: none;
			width: 100%; }
		.artcl:nth-child(4) {
			.block {
				column-count: 1;
				li {
					list-style: none; }
				li:before {
					content: '\2022';
					color: $green; } } } }

	.what-we-do:after {
		width: 100%;
		transform: translateX(0%);
		top: 40px; }

	.none375 {
		display: none; }
	.none1024 {
		display: block; }
	.site-h {
		margin-top: 0px;
		margin-bottom: 25px; }

	.tech {
		width: $w375;
		margin-bottom: 25px;
		.tech-block {
			// display: block
			width: 100%;
			margin: 0;
			margin-bottom: 20px;
			.img {
				// float: left
				width: 50px;
				height: 50px;
				// clear: both
				// height: 39px
				margin: 0;
				display: inline-block; }
			.text {
				width: 230px;
				font-size: 16px;
				display: inline-block;
				text-align: left;
				margin-left: 30px;
				div {
					font-size: 14px; } } }

		.tech-block:nth-child(1) {
			.text {
				div {
					width: 100%;
					margin: auto; } } } } }

