@import "mixin";

@import "header";

@include site-h;


body {
	min-width: 1120px;
	margin: 0 auto; }
.bunner {
	border-bottom: 2px solid lighten($text, 30%); }
.st-container {
	.logo {
		div {
			color: $text; } }
	nav {
		a {
			color: $text; } } }

.what-we-do {
	width: $width;
	max-width: 95%;
	margin: 0 auto 70px;
	padding-bottom: 70px;
	font-family: "FuturaPTB";
	.small-h {
		font-size: 30px;
		text-align: left;
		margin-top: 45px;
		margin-bottom: 20px; }
	.block {
		font-size: 18px;
		color: $text;
		display: inline-block;
		text-align: justify;
		span {
			color: $green; } }

	.artcl:nth-child(1) .block {
		display: block; }
	.artcl:nth-child(2) {
		float: left;
		width: 48%; }
	.artcl:nth-child(3) {
		float: right;
		width: 48%; }

	.artcl:nth-child(4) {
		float: right;
		width: 48%;
		.block {
			column-count: 2; } } }

.what-we-do:after {
	content: '';
	display: block;
	clear: both;
	height: 2px;
	background: $text;
	width: 200%;
	transform: translateX(-25%);
	position: relative;
	top: 70px; }

.none1024 {
	display: none; }

.tech {
	width: $width;
	max-width: 95%;
	margin: auto;
	margin-bottom: 70px;
	font-family: "FuturaPT";
	font-size: 24px;
	color: $text;
	display: -webkit-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	.tech-block {
		width: 240px;
		margin: 20px;
		text-align: center;
		display: inline-block;
		.img {
			width: 150px;
			height: 90px;
			margin: 0 auto 20px; }
		.text {
			div {
				font-size: 16px;
				color: lighten($text, 30%); } } }

	.tech-block:nth-child(1) {
		.img {
			background: url("../image/Ico-7.png") no-repeat 50% 50%;
			background-size: contain; }
		.text {
			div {
				width: 170px;
				margin: auto; } } }
	.tech-block:nth-child(2) {
		.img {
			background: url("../image/Ico-8.png") no-repeat 50% 50%;
			background-size: contain; } }
	.tech-block:nth-child(3) {
		.img {
			background: url("../image/Ico-9.png") no-repeat 50% 50%;
			background-size: contain; } }
	.tech-block:nth-child(4) {
		.img {
			background: url("../image/Ico-10.png") no-repeat 50% 50%;
			background-size: contain; } }
	.tech-block:nth-child(5) {
		.img {
			background: url("../image/Ico-11.png") no-repeat 50% 50%;
			background-size: contain; } }
	.tech-block:nth-child(6) {
		.img {
			background: url("../image/Ico-12.png") no-repeat 50% 50%;
			background-size: contain; } } }
@import "footer";

